@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
html {
  box-sizing: border-box;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  min-width: 320px;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.page__body-lock {
  height: 100vh;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #000000;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  text-align: center;
  margin-top: 0;
}
@media (max-width: 1023px) {
  .title {
    font-size: 24px;
    line-height: 41px;
  }
}

.page-header {
  color: #ffffff;
  background-color: #131314;
  background-image: url("../img/bike-bg-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/bike-bg-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .page-header {
    background-image: none;
    background-color: #ffffff;
  }
}

.page-header__wrapper {
  margin: 0 auto;
  max-width: 900px;
  padding: 0 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1023px) {
  .page-header__wrapper {
    display: flex;
    flex-direction: column;
    padding: 0;
    max-width: 100%;
  }
}

.page-header__info {
  padding-top: 255px;
  padding-bottom: 220px;
}
@media (max-width: 1023px) {
  .page-header__info {
    position: relative;
    background-color: #131314;
    background-image: url("../img/bike-bg-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 0;
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .page-header__info {
    background-image: url("../img/bike-bg-desktop@2x.jpg");
  }
}
@media (max-width: 320px) {
  .page-header__info {
    background-image: url("../img/bike-bg-mobile@1x.jpg");
  }
}
@media (max-width: 320px) and (min-resolution: 2dppx) {
  .page-header__info {
    background-image: url("../img/bike-bg-mobile@2x.jpg");
  }
}

.page-header__info a {
  display: inline-block;
}
@media (max-width: 1023px) {
  .page-header__info a {
    margin-top: 10px;
    margin-bottom: 0;
    position: absolute;
    left: 21px;
    top: 15px;
  }
}
@media (max-width: 767px) {
  .page-header__info a {
    left: 18px;
  }
}

.page-header h1 {
  margin-top: 25px;
  margin-bottom: 24px;
  max-width: 393px;
  font-size: 46px;
  line-height: 64px;
  font-weight: 800;
}
@media (max-width: 1023px) {
  .page-header h1 {
    margin: 0 auto;
    text-align: center;
    max-width: 525px;
    font-size: 30px;
    line-height: 42px;
    margin-bottom: 20px;
    margin-top: 134px;
  }
}
@media (max-width: 767px) {
