    margin-top: 121px;
  }
}

.page-header__info p {
  max-width: 381px;
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  opacity: 0.8;
}
@media (max-width: 1023px) {
  .page-header__info p {
    margin: 0 auto;
    text-align: center;
    max-width: 435px;
    font-size: 18px;
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .page-header__info p {
    padding: 0 15px;
    margin-bottom: 69px;
  }
