.main-nav {
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  padding: 0 10px;
  grid-column: 1/3;
}
@media (max-width: 1023px) {
  .main-nav {
    position: relative;
    padding: 0;
    margin: 0;
    border: none;
  }
}

.main-nav__list {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 0 14px;
  font-size: 16px;
  line-height: 32px;
  padding-top: 21px;
  padding-bottom: 8px;
  opacity: 0.7;
  font-weight: 300;
  justify-content: flex-start;
  column-gap: 133px;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .main-nav__list {
    display: block;
  }
}

@media (max-width: 1023px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__list {
    padding: 0;
    padding-top: 100px;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    justify-content: flex-start;
    z-index: 999;
    background-color: #ffffff;
    opacity: 1;
    color: #48494d;
  }
}

@media (max-width: 1023px) {
  .body--no-js .main-nav__list {
    display: flex;
    padding: 0;
    padding-top: 20px;
    margin: 0;
    position: relative;
    width: 100%;
    flex-direction: column;
    justify-content: flex-start;
    z-index: 15;
    background-color: #ffffff;
    opacity: 1;
    color: #48494d;
  }
}

.main-nav__button {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__button {
    display: block;
    z-index: 1000;
    position: absolute;
    right: 0;
    width: 50px;
    height: 50px;
    border: none;
    cursor: pointer;
  }
}

.main-nav--closed .main-nav__button {
  background: #0ad9c6;
}

.main-nav--opened .main-nav__button {
  background: #ffffff;
  position: fixed;
}

.body--no-js .main-nav__button {
  display: none;
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .main-nav__toggle {
    display: block;
    position: absolute;
    z-index: 12;
    right: 0;
    bottom: 0;
    height: 1px;
  }
}

@media (max-width: 1023px) {
  .main-nav--closed .main-nav__toggle {
    width: 22px;
    background-color: #000000;
    top: 25px;
    left: 14px;
  }
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  content: "";
  top: -6px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 22px;
  height: 1px;
  z-index: 12;
  background-color: #000000;
}
.main-nav--closed .main-nav__toggle::after {
  position: absolute;
  content: "";
  top: 6px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 22px;
  height: 1px;
  z-index: 12;
  background-color: #000000;
}

@media (max-width: 1023px) {
  .main-nav--opened .main-nav__toggle {
    width: 30px;
    background-color: rgba(20, 21, 23, 0.2);
    top: 25px;
    left: 10px;
    transform: rotate(-45deg);
  }
}
.main-nav--opened .main-nav__toggle::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 30px;
  height: 1px;
  z-index: 12;
  transform: rotate(90deg);
  background-color: rgba(20, 21, 23, 0.2);
}

